import React, { Component } from "react";
import "./styles.css";
import TodoList from "./TodoList";
import SearchBar from "./SearchBar";
import { loadState, saveState } from "./localState";
import servers from './servers'

const InputMode = {
  add: 0,
  search: 1
};

const converTodosResponse = (res) => {
  return res.reduce((acc, cur) => {
    acc.todos.push(cur.id);
    acc.todosById[cur.id] = {
      ...cur,
      isChecked: cur.isCompleted
    };
    return acc;
  }, {
    todos: [],
    todosById: {}
  });
}

export default class App extends Component {
  constructor(props) {
    super(props);
    this.handleOnTextChange = this.handleOnTextChange.bind(this);
    this.handleOnSubmit = this.handleOnSubmit.bind(this);
    this.handleOnItemDelete = this.handleOnItemDelete.bind(this);
    this.handleOnItemCheckChange = this.handleOnItemCheckChange.bind(this);
    this.getAllItemsFromServers = this.getAllItemsFromServers.bind(this);
    this.addItem = this.addItem.bind(this);
    this.searchByContent = this.searchByContent.bind(this);
    this.handleSwitchInputModeBtnClick = this.handleSwitchInputModeBtnClick.bind(
      this
    );
    this.state = loadState() || {
      todos: [],
      todosById: {

      },
      textValue: "",
      filteredResult: [],
      inputMode: InputMode.add,
    };
  }

  componentDidMount() {
    this.getAllItemsFromServers()
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.state !== prevState) {
      saveState(this.state);
      if (
        this.state.inputMode !== prevState.inputMode &&
        prevState.inputMode === InputMode.search
      ) {
        this.setState({
          filteredResult: []
        });
      }
    }
  }

  async getAllItemsFromServers() {
    try {
      const res = await servers.todo.getAllItems()
      const todoData = converTodosResponse(res);
      this.setState({ ...todoData })
    } catch (error) {
      // ignore error      
    }
  }

  handleSwitchInputModeBtnClick() {
    const { inputMode } = this.state;
    this.setState({
      inputMode: inputMode === InputMode.add
        ? InputMode.search
        : InputMode.add
    });
  }

  handleOnTextChange(e) {
    this.setState({ textValue: e.target.value });
  }

  async addItem() {
    const { textValue, todos, todosById } = this.state;
    try {
      const res = await servers.todo.postNewItem({ content: textValue })
      this.setState({
        todos: [res.id, ...todos],
        todosById: {
          ...todosById,
          [res.id]: {
            ...res,
            isChecked: res.isCompleted
          }
        },
        textValue: ""
      });
    } catch (error) {
      // error handle
    }
  }

  async searchByContent() {
    const { textValue, todosById } = this.state;
    try {
      const res = await servers.todo.getAllItems(`keyword=${textValue}`);
      const todoData = converTodosResponse(res);
      this.setState({
        filteredResult: todoData.todos,
        todosById: {
          ...todosById,
          ...todoData.todosById
        },
        textValue: ""
      })
    } catch (error) {
      // ignore error
    }
  }

  async handleOnSubmit(e) {
    e.preventDefault();

    const { textValue, inputMode } = this.state;

    if (!textValue) {
      this.setState({ textValue: '' })
      return undefined;
    }

    if (inputMode === InputMode.add) {
      this.addItem();
    } else {
      this.searchByContent();
    }
  }

  async handleOnItemDelete(id) {
    const { todosById, todos, filteredResult } = this.state;
    try {
      await servers.todo.deleteItemById(id);
      const todosByIdCopied = { ...todosById };
      delete todosByIdCopied[id];
      this.setState({
        todos: todos.filter(todoId => todoId !== id),
        todosById: todosByIdCopied,
        filteredResult: filteredResult.filter(todoId => todoId !== id),
      });
    } catch (error) {
      // ignore error
    }
  }

  async handleOnItemCheckChange(id) {
    const { todosById } = this.state;
    const todosCopied = { ...todosById };
    const todoWillBeModify = todosCopied[id];
    const todoIsChecked = todosCopied[id].isChecked;
    try {
      await servers.todo.patchItemIsCompleted({ id: id, isCompleted: !todoIsChecked });
      todoWillBeModify.isChecked = !todoIsChecked;
      this.setState({ todosById: todosCopied });
    } catch (error) {
      // ignore error
      console.log(error);
    }
  }

  render() {
    const {
      textValue,
      todos,
      filteredResult,
      inputMode,
      todosById
    } = this.state;
    const submitBtnText = inputMode === InputMode.add ? "add" : "search";
    const {
      handleOnTextChange,
      handleOnSubmit,
      handleOnItemDelete,
      handleOnItemCheckChange,
      handleSwitchInputModeBtnClick
    } = this;

    const listDataSource = (
      inputMode === InputMode.add
        ? todos
        : filteredResult
    ).map(todoId => todosById[todoId]);

    return (
      <div className="App">
        <div className="page-header">
          <div className="page-title">Todo</div>
          <button
            className="header__switch-input-mode-btn"
            onClick={handleSwitchInputModeBtnClick}
          >
            <span role="img" aria-label="search">
              {inputMode === InputMode.add ? "üîç" : "‚ûï"}
            </span>
          </button>
        </div>
        <SearchBar
          textValue={textValue}
          onSubmit={handleOnSubmit}
          submitBtnText={submitBtnText}
          onTextChange={handleOnTextChange}
        />
        <TodoList
          todos={listDataSource}
          onItemCheckChange={handleOnItemCheckChange}
          onItemDelete={handleOnItemDelete}
        />
      </div>
    );
  }
}
