const createFetchInstants = (config = {}) => {
  const fetch = (url, data, opt) => {
    const fetchOptons = {
      method: opt.method, // *GET, POST, PUT, DELETE, etc.
      headers: config.headers,
      cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
      credentials: 'same-origin', // include, same-origin, *omit
      headers: {
        'user-agent': 'Mozilla/4.0 MDN Example',
        'content-type': 'application/json'
      },
      mode: 'cors', // no-cors, cors, *same-origin
      redirect: 'follow', // manual, *follow, error
      referrer: 'no-referrer', // *client, no-referrer
      ...config
    }

    const method = config?.headers?.method || opt.method
    if (!['GET', 'DELETE'].includes(method)) {
      fetchOptons['body'] = JSON.stringify(data)
    }

    return window.fetch(`${config.baseUrl || ""}${url}`, fetchOptons)
      .then(
        async (res) => {
          if (res.status > 299 || res.status < 200) {
            throw new Error('ËØ∑Ê±ÇÈîôËØØ')
          }

          const json = await res.json()
          if (typeof config.afterResponse === "function") {
            return config.afterResponse(json)
          } else {
            return json
          }
        }
      )
  }

  return {
    get(url, opt = {}) {
      return fetch(url, null, { method: 'GET', ...opt })
    },
    post(url, data, opt = {}) {
      return fetch(url, data, { method: 'POST', ...opt })
    },
    delete(url, opt = {}) {
      return fetch(url, null, { method: 'DELETE', ...opt })
    },
    patch(url, data, opt = {}) {
      return fetch(url, data, { method: 'PATCH', ...opt })
    },
  }
}

const fetchConfig = {
  baseUrl: '/api',
  afterResponse: res => {
    return res.data
  },
}

const fetch = createFetchInstants(fetchConfig)

export default fetch;