import fetch from './fetch';

export default {
  getAllItems(query='') {
    return fetch.get(`/items?${query}`)
  },
  postNewItem({ content }) {
    return fetch.post(`/items`, { content: content })
  },
  deleteItemById(id) {
    return fetch.delete(`/items/${id}`)
  },
  patchItemIsCompleted({id, isCompleted}) {
    return fetch.patch(`/items/${id}/isCompleted`, {isCompleted: isCompleted})
  },
};